import React from 'react';
import ReactDOM from 'react-dom';
import 'normalize.css';
import './public/styles/preload.css';
import './public/styles/animate.css';
import Index from './containers/home/index';
import Axios from 'axios';
import Request from './utils/Axios';
import PreLoad from './utils/Preload';
import WeiXin from './utils/Weixin';
import registerServiceWorker from './registerServiceWorker';
const BaseUrl = window.BaseUrl;
Request(Axios);

/* 资源加载 */
const Manifest = [
    { id: "gif1", src: "1.gif" },
    { id: "gif2", src: "2.gif" },
    { id: "gif3", src: "3.gif" },
    { id: "gif4", src: "4.gif" },
    { id: "gif5", src: "5.gif" },
    { id: "gif6", src: "6.gif" },
    { id: "gif7", src: "7.gif" },
    { id: "gif8", src: "8.gif" },
    { id: "gif9", src: "9.gif" },
    { id: "gif10", src: "10.gif" },
    { id: "gifs", src: "s-1.gif" },

    { id: "fttf", src: "fz.ttf" },

    { id: "p2", src: "p-2-0.jpg" },
    { id: "p2bg", src: "page2-bg.png" },
    { id: "p2car", src: "page2-car.png" },

    { id: "p3", src: "p-3-0.jpg" },
    { id: "p5", src: "p-5-0.jpg" },
    { id: "p5kv", src: "p5-kv.png" },

    { id: "mp3", src: "bg.mp3" },

    { id: "p1-1", src: "p1-1.mp3" },
    { id: "p1-2-old", src: "p1-2.mp3" },
    { id: "p1-2", src: "p1-no.mp3" },

    { id: "p2-1", src: "p2-1.mp3" },
    { id: "p2-car", src: "p2-car.mp3" },
    { id: "xu", src: "xu.mp3" },

    { id: "p3-switch", src: "p3-switch.mp3" },

    { id: "p4-1", src: "p4-1.mp3" },
    { id: "p4-2", src: "p4-2.mp3" },
    { id: "p4-3", src: "p4-3.mp3" },
    { id: "p4-4", src: "p4-4.mp3" },
];
// 在这里进行资源的加载&微信签名的请求和配置


WeiXin(Axios);
const PreLoadDom = document.getElementById("preload");
const Root = document.getElementById("root");
const btn = document.querySelector("#btn");

document.querySelector("#btn").addEventListener("touchstart", function () {
    PreLoadDom.parentNode.removeChild(PreLoadDom);
    Root.style.display = 'block';
    ReactDOM.render(<Index />, Root);
    registerServiceWorker();
});


/* 资源加载，加载成功之后再渲染内容 */
window.createJS = PreLoad({
    BaseUrl: BaseUrl,
    Manifest: Manifest,
    ProgressDom: document.querySelector(".p-progress-inner"),
    ProgressText: document.querySelector(".p-progress-text"),
    callback: () => {

        // 静态资源加载完成之后，初始化界面
        // document.getElementById('btn').click();


        // document.querySelector("body").click();

        btn.style.visibility = "visible";

        // btn.onclick = function () {
        //     PreLoadDom.parentNode.removeChild(PreLoadDom);
        //     alert(1);

        //     Root.style.display = 'block';
        //     ReactDOM.render(<Index />, Root);
        //     registerServiceWorker();
        // }

    }
});





