import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import './layout.css';
import SlideData from './data';
const createjs = window.createjs;
const swiperAnimateCache = window.swiperAnimateCache;
const swiperAnimate = window.swiperAnimate;
const Swiper = window.Swiper;
const JumpUrl = window.JumpUrl;
let TypeTimeOut = null;
var props = new createjs.PlayPropsConfig().set({ interrupt: createjs.Sound.INTERRUPT_ANY, loop: -1, volume: 0.2 });
class App extends Component {

    constructor(props) {
        super(props);
        this.state = {
            pagenum: 0,
            videoflag: true,
            showShareMask: false,
            page2bg: require('./imgs/p-2-0.jpg'),
            share: require('./imgs/s-1.gif')
        }
    }

    componentWillMount() {
        // 背景音乐
        this.mp3 = createjs.Sound.play('mp3', props);
    }

    componentDidMount() {
        let self = this;

        createjs.CSSPlugin.install(createjs.Tween);

        // 页面整体的轮播切换
        this.Swiper = new Swiper(self.refs.main, {
            // effect: 'fade',
            noSwiping: true,
            initialSlide: 0,
            on: {
                init: function () {
                    swiperAnimateCache(this); //隐藏动画元素
                    swiperAnimate(this); //初始化完成开始动画

                    var PropsOne = new createjs.PlayPropsConfig().set({ interrupt: createjs.Sound.INTERRUPT_ANY,delay:500,volume: 0.5 });
                    createjs.Sound.play('p1-1',PropsOne);
                    var PropsNo = new createjs.PlayPropsConfig().set({ interrupt: createjs.Sound.INTERRUPT_ANY,delay:3000,volume: 0.1 });
                    createjs.Sound.play('p1-2',PropsNo);
                    

                    let page1 = self.refs.page1;
                    createjs.Tween.get(page1)
                        .wait(5000)
                        .call(() => {
                            self.SwitchPage(1);
                        });

                },
                slideChangeTransitionEnd: function () {
                    swiperAnimate(this); //每个slide切换结束时也运行当前slide动画
                    self.setState({
                        pagenum: this.activeIndex
                    });


                    if (this.activeIndex === 1) {
                        var PropsVideo = new createjs.PlayPropsConfig().set({ interrupt: createjs.Sound.INTERRUPT_ANY, volume: 1 });
                        self.p2video = createjs.Sound.play('p2-1', PropsVideo);

                        var PropsCar = new createjs.PlayPropsConfig().set({ interrupt: createjs.Sound.INTERRUPT_ANY, loop: -1, volume: 0.5 });
                        self.p2car = createjs.Sound.play('p2-car', PropsCar);

                        var PropsXu = new createjs.PlayPropsConfig().set({ interrupt: createjs.Sound.INTERRUPT_ANY,delay:32000, volume: 0.5 });
                        self.p2videox = createjs.Sound.play('xu', PropsXu);


                    } else {
                        self.p2video && self.p2video.stop();
                        self.p2car && self.p2car.stop();
                        self.p2videox && self.p2videox.stop();
                    }

                    if (this.activeIndex === 3) {
                        createjs.Sound.play('p4-1');


                        self.delay(3000).then(() => {
                            createjs.Sound.play('p4-2');
                        });

                        self.delay(6000).then(() => {
                            createjs.Sound.play('p4-3');
                        });

                        self.delay(9500).then(() => {
                            createjs.Sound.play('p4-4');
                        });
                    }

                }
            }

        });


        // 第三屏的切换
        this.SwiperPage3 = new Swiper(self.refs.swiper, {
            loop: true,
            navigation: {
                nextEl: '.swiper-button-next',
                prevEl: '.swiper-button-prev',
            },
            on: {
                slideChangeTransitionEnd: function () {
                    console.log(this.activeIndex);
                    if (this.activeIndex !== 1) {
                        createjs.Sound.play('p3-switch');
                    }

                }
            }
        });

    }

    delay(time) {
        return new Promise(function (resolve, reject) {
            setTimeout(function () {
                console.log(`延迟${time}秒`);
                resolve();
            }, time);
        })
    }

    componentDidUpdate(a, b) {
        let self = this;
        if (this.state.pagenum == 3) {
            let p4text4 = this.refs.page4;

            createjs.Tween.get(p4text4)
                .wait(16000)
                .call(() => {
                    self.SwitchPage(4);
                });

        }

    }


    /* 音乐按钮的开关操作 */
    videoSwitch() {
        this.setState({
            videoflag: !this.state.videoflag
        });
        if (this.state.videoflag) {
            // createjs.Sound.stop();
            this.mp3.paused = true;
        } else {
            this.mp3.paused = false;
            // createjs.Sound.play('mp3',props);
        }

    }

    /* 切换页面 */
    SwitchPage(num) {
        this.setState({
            pagenum: num
        });
        this.Swiper.slideTo(num);
    }

    /* 显示分享遮罩层 */
    TiggerShareMask() {
        this.setState({
            showShareMask: !this.state.showShareMask
        });
    }

    /* 跳转页面 */
    jumpToForm() {
        window.location.href = JumpUrl;
    }
    render() {
        const pagenum = this.state.pagenum;

        /* 第1屏 */
        const page1 = <div ref='page1' className='page page1 swiper-slide swiper-no-swiping'>
            <div
                swiper-animate-effect="fadeOutUp"
                swiper-animate-duration="0.5s"
                swiper-animate-delay="2.8s" className='ani page1-text'>
                <div
                    swiper-animate-effect="fadeInUp"
                    swiper-animate-duration="0.5s"
                    swiper-animate-delay="0.5s" className='ani'>
                    你很好，但我们不适合。</div>
            </div>
            <div
                swiper-animate-effect="fadeInUp"
                swiper-animate-duration="0.5s"
                swiper-animate-delay="2.8s" className='ani page1-text'>不</div>
        </div>;


        let stageCls, carCls;
        if (this.state.pagenum === 1) {
            stageCls = 'page2-stage-ani page2-stage-img';
            carCls = 'page2-car page2-car-2 page2-car-ani';
        } else {
            stageCls = 'page2-stage-img';
            carCls = 'page2-car page2-car-2';
        }

        /* 第2屏 */
        const page2 = <div className='page page2 swiper-slide swiper-no-swiping'>

            {/* 场景 */}
            <div className="page2-stage">
                <div className={stageCls}></div>
            </div>
            {/* 汽车 */}
            {/* <div className='page2-car page2-car-1'></div> */}
            {/* 汽车 */}
            <div className={carCls}></div>

            {/* 文字部分 */}
            <div className='page2-text'>

                <div className="ani"
                    swiper-animate-effect="fadeOutUp"
                    swiper-animate-duration="0.5s"
                    swiper-animate-delay="3.7s" >
                    <p
                        className="ani"
                        swiper-animate-effect="fadeInUp"
                        swiper-animate-duration="0.5s"
                        swiper-animate-delay="0.1s">我是一个HR，做招聘已经8年，
                    </p>
                </div>

                <div className="ani"
                    swiper-animate-effect="fadeOutUp"
                    swiper-animate-duration="0.5s"
                    swiper-animate-delay="7.2s" >
                    <p
                        className="ani"
                        swiper-animate-effect="fadeInUp"
                        swiper-animate-duration="0.5s"
                        swiper-animate-delay="3.7s"
                    >我给你讲，招人就像找对象，</p>
                </div>

                <div className="ani"
                    swiper-animate-effect="fadeOutUp"
                    swiper-animate-duration="0.5s"
                    swiper-animate-delay="10.5s" >
                    <p
                        className="ani"
                        swiper-animate-effect="fadeInUp"
                        swiper-animate-duration="0.5s"
                        swiper-animate-delay="7.2s"
                    >每天都在与无数人擦肩而过，</p>
                </div>

                <div className="ani"
                    swiper-animate-effect="fadeOutUp"
                    swiper-animate-duration="0.5s"
                    swiper-animate-delay="13.5s" >
                    <p
                        className="ani"
                        swiper-animate-effect="fadeInUp"
                        swiper-animate-duration="0.5s"
                        swiper-animate-delay="10.5s"
                    >每天都在寻找那个合适的ta，</p>
                </div>

                <div className="ani"
                    swiper-animate-effect="fadeOutUp"
                    swiper-animate-duration="0.5s"
                    swiper-animate-delay="17.8s" >
                    <p
                        className="ani"
                        swiper-animate-effect="fadeInUp"
                        swiper-animate-duration="0.5s"
                        swiper-animate-delay="13.5s"
                    >每天都面临着被拒绝。</p>
                </div>

                <div className="ani"
                    swiper-animate-effect="fadeOutUp"
                    swiper-animate-duration="0.5s"
                    swiper-animate-delay="19.3s" >
                    <p
                        className="ani"
                        swiper-animate-effect="fadeInUp"
                        swiper-animate-duration="0.4s"
                        swiper-animate-delay="18.5s"
                    >这些年，</p>
                </div>

                <div className="ani"
                    swiper-animate-effect="fadeOutUp"
                    swiper-animate-duration="0.5s"
                    swiper-animate-delay="22s" >
                    <p className="ani"
                        swiper-animate-effect="fadeInUp"
                        swiper-animate-duration="0.5s"
                        swiper-animate-delay="19.3s">我约过的候选人形形色色，</p>
                </div>


                <div className="ani"
                    swiper-animate-effect="fadeOutUp"
                    swiper-animate-duration="0.5s"
                    swiper-animate-delay="23s" >
                    <p className="ani"
                        swiper-animate-effect="fadeInUp"
                        swiper-animate-duration="0.5s"
                        swiper-animate-delay="22s">与他们之间的故事，</p>
                </div>


                <div className="ani"
                    swiper-animate-effect="fadeOutUp"
                    swiper-animate-duration="0.5s"
                    swiper-animate-delay="26s" >
                    <p className="ani"
                        swiper-animate-effect="fadeInUp"
                        swiper-animate-duration="0.5s"
                        swiper-animate-delay="23s">精彩程度堪比小说，跌宕起伏。</p>
                </div>

                <div className="ani"
                    swiper-animate-effect="fadeOutUp"
                    swiper-animate-duration="0.5s"
                    swiper-animate-delay="27.8s" >
                    <p className="ani"
                        swiper-animate-effect="fadeInUp"
                        swiper-animate-duration="0.3s"
                        swiper-animate-delay="27s">现在，</p>
                </div>

                <div className="ani"
                    swiper-animate-effect="fadeOutUp"
                    swiper-animate-duration="0.5s"
                    swiper-animate-delay="30s" >
                    <p className="ani"
                        swiper-animate-effect="fadeInUp"
                        swiper-animate-duration="0.5s"
                        swiper-animate-delay="27.8s">国内第一篇根据真人真事整理的</p>
                </div>

                <div className="ani"
                    swiper-animate-effect="fadeOutUp"
                    swiper-animate-duration="0.5s"
                    swiper-animate-delay="32.5s" >
                    <p className="ani"
                        swiper-animate-effect="fadeInUp"
                        swiper-animate-duration="0.5s"
                        swiper-animate-delay="30s">《如何假装成一个人人都爱的HR》爆文</p>
                </div>

                <div className="ani"
                    swiper-animate-effect="fadeOutUp"
                    swiper-animate-duration="0.5s"
                    swiper-animate-delay="34s" >
                    <p className="ani"
                        swiper-animate-effect="fadeInUp"
                        swiper-animate-duration="0.3s"
                        swiper-animate-delay="32.5s">正式上线，</p>
                </div>



                <p className="ani"
                    swiper-animate-effect="fadeInUp"
                    swiper-animate-duration="0.5s"
                    swiper-animate-delay="34s">心酸揭露HR这行男默女泪的纠葛。</p>


            </div>
            <div onClick={this.SwitchPage.bind(this, 2)} className='page2-btn'></div>
        </div>;



        /* 第三屏幕 */
        const RenderSlide = SlideData.map((item, key) => {
            return <div key={key} className="swiper-slide swiper-slide1">
                <div className='s-header'>
                    <div className='s-pinyin s-p'>
                        {
                            item.p.map((p, k) => {
                                return (<span key={k}>{p}</span>)
                            })
                        }
                    </div>
                    <div className='fzyk s-pinyin s-title'>
                        {
                            item.t.map((p, k) => {
                                return (<span key={k}>{p}</span>)
                            })
                        }
                    </div>
                </div>
                <div className='s-body'>
                    <img src={item.img} />
                </div>
                <div className='s-footer'>
                    <div className='fzyk s-f-title'>「释义」：</div>
                    <div className='fzyk s-f-desc'>{item.desc}</div>
                </div>
            </div>;
        })

        const page3 = <div className='page page3 swiper-slide'>
            <div ref='swiper' className="swiper-container swiper-container1">
                <div className="swiper-wrapper">
                    {RenderSlide}
                </div>
                <div className="swiper-button-next"></div>
                <div className="swiper-button-prev"></div>
            </div>
            <div onClick={this.SwitchPage.bind(this, 3)} className='page3-btn'></div>
        </div>;

        /* 第四屏*/
        const page4 = <div ref="page4" className='page page4 swiper-slide swiper-no-swiping'>

            <div
                swiper-animate-effect="fadeOutUp"
                swiper-animate-duration="0.5s"
                swiper-animate-delay="2.5s" className='ani'>
                <p
                    swiper-animate-effect="fadeInUp"
                    swiper-animate-duration="0.5s"
                    swiper-animate-delay="0.1s"
                    className='ani page4-text'>你很好，我觉得你们最合适</p>
            </div>

            <div
                swiper-animate-effect="fadeOutUp"
                swiper-animate-duration="0.5s"
                swiper-animate-delay="6s" className='ani'>

                <p
                    swiper-animate-effect="fadeInUp"
                    swiper-animate-duration="0.5s"
                    swiper-animate-delay="2.5s"
                    className='ani page4-text'>是嘛，你怎么知道我们最适合？</p>
            </div>

            <div
                swiper-animate-effect="fadeOutUp"
                swiper-animate-duration="0.5s"
                swiper-animate-delay="10s" className='ani'>
                <p
                    swiper-animate-effect="fadeInUp"
                    swiper-animate-duration="0.5s"
                    swiper-animate-delay="6s"
                    className='ani page4-text'>因为我懂社交，懂招聘，所以我更懂你。</p>
            </div>




            <p
                swiper-animate-effect="fadeInUp"
                swiper-animate-duration="0.5s"
                swiper-animate-delay="10s"
                className='ani page4-text page4-text-big'>脉脉，
                <span
                    swiper-animate-effect="fadeInUp"
                    swiper-animate-duration="0.5s"
                    swiper-animate-delay="11s"
                    className='ani page4-text page4-text-big'>懂社交，</span>

                <span
                    swiper-animate-effect="fadeInUp"
                    swiper-animate-duration="0.5s"
                    swiper-animate-delay="12s"
                    className='ani page4-text page4-text-big'>懂招聘，</span>

                <span
                    swiper-animate-effect="fadeInUp"
                    swiper-animate-duration="0.5s"
                    swiper-animate-delay="13s"
                    className='ani page4-text page4-text-big'>更懂你</span>

            </p>





        </div>;

        /* 第5屏*/
        const page5 = <div className='page page5 swiper-slide swiper-no-swiping'>
            <div
                swiper-animate-effect="swing"
                swiper-animate-duration="0.5s"
                swiper-animate-delay="1.5s"

                className="page5-kv ani ">
                <div swiper-animate-effect="bounceIn"
                    swiper-animate-duration="1s"
                    swiper-animate-delay="0.2s" className="ani page5-kv-inner"></div>
            </div>
            <div className='page5-btn-group'>
                <div onClick={this.jumpToForm.bind(this)} className='page5-btn page5-btn-form'></div>
                <div onClick={this.TiggerShareMask.bind(this)} className='page5-btn page5-btn-share'></div>
            </div>
        </div>;

        // 音乐按钮
        let MusicWhiteClassName, MusicBlackClassName;
        if (!this.state.videoflag) {
            MusicWhiteClassName = 'music music-white music-white-pause';
            MusicBlackClassName = 'music music-black music-black-pause';
        } else {
            MusicWhiteClassName = 'music music-white';
            MusicBlackClassName = 'music music-black';
        }
        const MusicWhite = <div ref='music' onClick={this.videoSwitch.bind(this)} className={MusicWhiteClassName}></div>;
        const MusicBlack = <div ref='music' onClick={this.videoSwitch.bind(this)} className={MusicBlackClassName}></div>;
        let RenderPage, RenderMusic;

        switch (this.state.pagenum) {
            case 0:
            case 3:
                RenderMusic = MusicWhite;
                break;
            case 1:
            case 2:
                RenderMusic = MusicBlack;
                break;
            case 4:
                RenderPage = page5;
                break;
        }

        /* 分享遮罩层 */
        const RenderShareMask = <div onClick={this.TiggerShareMask.bind(this)} className='page-share'>
            <div className='share-img'>
                <img src={this.state.share} />
            </div>
        </div>


        return (
            <div className="main">
                {/* 音乐按钮 */}
                {RenderMusic}
                {/* 分享遮罩层 */}
                {this.state.showShareMask && RenderShareMask}
                {/* 主页面 */}

                <div ref='main' className="swiper-container">
                    <div className="swiper-wrapper">
                        {page1}
                        {page2}
                        {page3}
                        {page4}
                        {page5}
                    </div>
                </div>
            </div>
        );
    }
}

export default App;



